table {
  width: 100%;
  border-spacing: 0;
  overflow: hidden;
}

table tbody tr:nth-of-type(odd) {
  background-color: rgba(0, 0, 0, 0.05);
}

table tbody tr:hover {
  background-color: #F2FBFB;
}

table tbody tr:active, table tbody tr.active {
  background-color: #E5F6F7;
}

table td input {
  margin-bottom: 0;
}

table td, table th {
  padding: 1.2rem;
  border-bottom: 0.1rem solid #e9ecef;
  text-align: left;
  transition: all ease 0.2s;
}

table td:first-child, table th:first-child {
  padding-left: 0;
}

table td:last-child, table th:last-child {
  padding-right: 0;
}

thead {
  background-color: #fff;
  overflow: visible;
}

thead th {
  font-weight: normal;
  border-bottom: 0.2rem solid #ced4da;
  white-space: nowrap;
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -o-user-select: none;
}

thead th:hover {
  cursor: default;
}

thead th.sortable i {
  color: #6c757d;
}

thead th.sortable:hover {
  cursor: pointer;
}

thead th.sortable.sort-active i {
  color: #212529;
}

.nodata {
  font-size: 1.4rem;
  font-weight: bold;
  text-align: center;
  color: #9b4dca;
}

b,
strong {
  font-weight: bold;
}

p {
  margin-top: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 300;
  letter-spacing: -.1rem;
  margin-bottom: 2.0rem;
  margin-top: 0;
}

h1 {
  font-size: 4.6rem;
  line-height: 1.2;
}

h2 {
  font-size: 3.6rem;
  line-height: 1.25;
}

h3 {
  font-size: 2.8rem;
  line-height: 1.3;
}

h4 {
  font-size: 2.2rem;
  letter-spacing: -.08rem;
  line-height: 1.35;
}

h5 {
  font-size: 1.8rem;
  letter-spacing: -.05rem;
  line-height: 1.5;
}

h6 {
  font-size: 1.6rem;
  letter-spacing: 0;
  line-height: 1.4;
}

img {
  max-width: 100%;
}

.clearfix:after {
  clear: both;
  content: ' ';
  display: table;
}

.float-left {
  float: left;
}

.float-right {
  float: right;
}
